.contracts_how {
  border-radius: 50px 50px 0 0;
  background: #fff;
  .container-fluid {
    width: 100%;
    padding: 0;
    margin: 0;
  }
  .container {
    max-width: 1440px;
    margin: 0 auto;
    padding: 80px 60px 0 60px;
    @media screen and (max-width: 1100px) {
      max-width: 1100px !important;
      padding: 32px 16px !important;
    }
    @media screen and (max-width: 768px) {
      max-width: 720px !important;
      padding: 32px 16px !important;
    }
    @media screen and (max-width: 480px) {
      max-width: 375px;
      padding: 32px 16px;
    }
  }
  .two_boxes {
    display: flex;
    gap: 60px;
    place-items: center;
    @media screen and (max-width: 768px) {
      flex-direction: column;
      gap: 40px;
    }
    .intro {
      display: block;
      gap: 12px;
      margin-bottom: 40px;
      max-width: 600px;
      place-content: center;
      width: 100%;
      @media screen and (max-width: 768px) {
        order: 2;
        max-width: 100% !important;
      }

      .title {
        display: grid;
        gap: 24px;
        padding: 0;

        h2 {
          margin: 0;
          font-weight: 500;
          color: #0a2244;
        }

        .hwp {
          max-width: 540px;
          width: 100%;
          color: #616d82;
        }
      }
    }
    .img {
      @media screen and (max-width: 768px) {
        order: 1;
      }
    }
  }
}

.radius-0 {
  border-radius: 0;
}
